.Footer {
  position: relative;
  bottom: -150px;
  padding-top: 12px;
  padding-bottom: 12px;

  margin-right: auto;
  margin-left: auto;
  width: 320px;

  background: white;

  text-align: center;
  font-size: 0.75rem;

  border-top:1px solid darkgray;
  border-right:1px solid darkgray;
  border-left:1px solid darkgray;
  border-radius: 15px 15px 0 0;
  -moz-border-radius: 15px 15px 0 0;
  -webkit-border-radius: 15px 15px 0 0;

  -webkit-box-shadow: 0 10px 101px -5px rgba(0,0,0,0.75);
  box-shadow: 0 10px 101px -5px rgba(0,0,0,0.75);
}

@media (max-width: 500px) {
  .Footer {
    width: 250px;
  }
}

.Footer a {
  font-weight: 400;
  text-decoration: none;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

.Footer a:hover {
  color: darkgrey;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}



