.Input-container {
  padding-top: 25px;
}

.Input-header {
  font-weight: bold;
  font-size: 1.17rem;
}

.Input-active, .Input-not-active {
  margin-top: 5px;
  padding: 8px;

  height: 20px;
  width: 150px;

  font-family: 'Open Sans', sans-serif;
  font-size: 0.9rem;
  font-weight: 300;
}

@media (max-width: 500px) {
  .Input-active, .Input-not-active {
    width: 160px;
  }
}

.Input-active::-webkit-input-placeholder {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  color: #ccc;
}

