.Colors {
  top: -3px;
}

.Colors-no-color {
  top: -93px;
}

.Colors-no-color .Colors-text-wrapper {
  padding-top: 115px;
}

.Colors, .Colors-no-color {
  position: relative;
  z-index: 0;

  width: 320px;
  height: 170px;

  margin-left: auto;
  margin-right: auto;

  background: white;

  border: 1px solid darkgray;
  border-radius: 0 0 15px 15px;
  -moz-border-radius: 0 0 15px 15px;
  -webkit-border-radius: 0 0 15px 15px;

  -webkit-box-shadow: 0 10px 101px -5px rgba(0,0,0,0.75);
  box-shadow: 0 10px 101px -5px rgba(0,0,0,0.75);

  -webkit-transition: 1s ease;

  -o-transition: 1s ease;

  transition: 1s ease;
}

@media (max-width: 500px) {
  .Colors, .Colors-no-color {
    width: 250px;
  }
}

.Colors-table {
  margin-left: auto;
  margin-right: auto;
}

.Colors-color-type {
  padding-right: 3px;
  text-align: right;
}

.Colors-color-value {
  font-size: 0.8rem;
  text-align: left;
}

.Colors-button-active {
  width: 58px;
  -webkit-transition: 0.25s ease-out;
  -o-transition: 0.25s ease-out;
  transition: 0.25s ease-out;
}

.Colors-button-not-active {
  width: 48px;
  -webkit-transition: 0.25s ease-in;
  -o-transition: 0.25s ease-in;
  transition: 0.25s ease-in;
}



